* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: #FFFAFA;
}
.banner-profile {
    box-shadow: 0px 5px 10px rgba(255, 207, 198, .3), 0px 15px 10px rgba(255, 207, 198, .1);
    display: flex;
    flex-direction: column;
    height: 50vh;
    
}
.banner-profile .img {
    width: 100%;
    height: 12em;
    overflow: hidden;
}
.banner-profile .img img {
    width: 100%;
}
.profile-p {
    position: relative;
    top: -2em;
    left: 2em;
    width: 4em;
}
.name-profile {
    display: flex;
    justify-content: space-between;
    margin: 0 1em 0 1em;
}
.name-profile .name {
    display: flex;
    justify-content: space-between;
    gap: 3em;
   
}
.n-p {
    line-height: .6em;
    margin-top: -.7em;
}
.folowers {
    display: flex;
    gap: 1em;
}
.n-p p{
    opacity: 0.2;
    font-weight: 600;
    font-size: .9rem;
}
.folowers p {
    font-weight: 600;
}
.folowers span {
    opacity: 0.2;
    font-size: .9rem;
}
.profile {
   align-self: flex-start;
   margin-left: 7.2em;
   margin-top: 2em;
}
.profile a {
    color: #000;
    text-decoration: none;
    font-weight: 600;
    margin-left: 1em;
    position: relative;
}
.profile .active::after {
    content: "";
    width: 40%;
    height: 3.8px;
    position: absolute;
    top: 1.5em;
    left: 20%;
    background-color:#638FFB;
}

.main-profile {
    display: grid;
    gap: 1em;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);;
}
.main-profile section{
    box-shadow: 0px 5px 10px rgba(255, 207, 198, .3), 0px 15px 10px rgba(255, 207, 198, .1);
}
.about {
    grid-area: 1 / span 3;
    padding: 1em;
}
.about-t {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 24em;
    margin-right: 2em;
    align-items: flex-end;
}
.about-p {
    display: flex;
    justify-content: space-between;
    
}
 .links-medsos {
     position: relative;
     bottom: 3em;
 }
 .links-medsos img {
     margin-left: .3em;
 }
.post-profile {
    grid-area: 2 / span 2;
    padding: 2em;
}
.activity {
    grid-area: 2/span 1;
    padding: 2em;
    line-height: .2em;
}
.friend-list {
    grid-area: span 2 / 4;
    padding: 2em;
}
.feed-profile {
    grid-area: 3 / span 3;
}
.linked-medsos img {
    opacity: 0.2;
}
.info-profile {
    display: flex;
    gap: 1em;
}
.write {
    display: flex;
    gap: .2em;
}
.write p {
    font-weight: 600;
    font-size: .85rem;
}
.i-post {
    display: flex;
    align-items: center;
    gap: 1.2em;
    justify-content: space-evenly;
}
.post {
    border: 1px solid rgba(0,0,0,0.1);
}
.create-post-profile input {
 width: 90%;
 border: none;

}
.create-post-profile {
    border: 1px solid rgba(0,0,0,0.1);
    width: 100%;
    height: 20vh;
    display: flex;
    
    flex-direction: column;
    justify-content: space-between;
}
.react-box {
    display: flex;
    padding: 0 1em .5em 1em;
    justify-content: space-between;
    gap: .2em;
    margin-left: 1em;
}
.bd-blue {
    position: relative;
    margin-bottom: 3em;
}

.bd-blue::before {
    position: absolute;
    content: "";
    width: 8%;
    height: 3px;
    background-color: #638FFB;
    top: 1.5em;
}
.activity a{
    text-decoration: none;
    color: #638FFB;
    font-weight: 600;
}
.activity a img{
    text-decoration: none;
    color: #638FFB;
    font-weight: 600;
    margin-top: 2em;
    margin: auto;
}
.friends-active {
    width: 20em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1em;
}
.friends-active p {
    font-size: .8rem;
    text-align: start;
}
.friends-active li {
    color: #7AC133;
}
.ac-people {
    display: flex;
    align-items: center;
    gap: .8em;
}
.friend-list a {
    color: #638FFB;
    text-decoration: none;
}